'use client'
import { useState, useEffect } from 'react'
import ServicePageMain from '@/app/Components/ServicePageMain'
import OurService from '@/app/Components/OurService'
import Footer from '@/app/Components/Footer'
import Navbar from '@/app/Components/Navbar'

function Service() {
  const [showNavbar, setShowNavbar] = useState(true)
  const [lastScrollY, setLastScrollY] = useState(0)

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > lastScrollY) {
        setShowNavbar(false) // Hide navbar when scrolling down
      } else {
        setShowNavbar(true) // Show navbar when scrolling up
      }
      setLastScrollY(window.scrollY)
    }

    window.addEventListener('scroll', handleScroll)
    return () => window.removeEventListener('scroll', handleScroll)
  }, [lastScrollY])

  return (
    <div className="min-h-screen relative" style={{
      background: `linear-gradient(to bottom right, #f8f9fa, #ffffff, #f1f3f5)`
    }}>
      <div className={`transition-transform duration-300 ${showNavbar ? 'translate-y-0' : '-translate-y-full'}`}>
        <Navbar />
      </div>
      <ServicePageMain/>
      <OurService/>
      <Footer/>
    </div>
  )
}

export default Service
